    import {getUserInfo,setUserInfo} from '../../service.js';
    import {mapState,mapMutations} from 'vuex'
    import mInput from '../../components/m-input.vue'
    export default {
        components: {mInput},
        data() {
            return {
                providerList: [],
                hasProvider: false,
                account: '',
                password: '',
                positionTop: 0,
				
				userF:false,
				passF:false,
				userimg:"../../static/img/login_user_default_icon.png",
				passimg:"../../static/img/login_pas_defaukt_icon.png"
            }
        },
        computed: mapState(['forcedLogin']),
		watch:{
			userF(val,oldval){
				this.userimg = val == true ? "../../static/img/login_user_selected_icon.png" : "../../static/img/login_user_default_icon.png";
			},
			passF(val,oldval){
				this.passimg = val == true ? "../../static/img/login_pas_selected_icon.png" : "../../static/img/login_pas_defaukt_icon.png";
			},
		},
		onLoad() {
			let userInfo = getUserInfo();
			if(userInfo.isLogin === true){
				uni.switchTab({
					url:"/pages/tabBar/mine/mine"
				})
			}
		},
        methods: {
			onFocus(){this.userF = true;},
			onBlur(){this.userF = false;},
			onFocusP(){this.passF = true;},
			onBlurP(){this.passF = false;},
			goF(){
				uni.navigateTo({
					url:"/pages/pwd/pwd"
				})
			},
            ...mapMutations(['login']),
            initProvider() {
                const filters = ['weixin', 'qq', 'sinaweibo'];
                uni.getProvider({
                    service: 'oauth',
                    success: (res) => {
                        if (res.provider && res.provider.length) {
                            for (let i = 0; i < res.provider.length; i++) {
                                if (~filters.indexOf(res.provider[i])) {
                                    this.providerList.push({
                                        value: res.provider[i],
                                        image: '../../static/img/' + res.provider[i] + '.png'
                                    });
                                }
                            }
                            this.hasProvider = true;
                        }
                    },
                    fail: (err) => {
                        console.error('获取服务供应商失败：' + JSON.stringify(err));
                    }
                });
            },
            initPosition() {
                /**
                 * 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
                 * 反向使用 top 进行定位，可以避免此问题。
                 */
                this.positionTop = uni.getSystemInfoSync().windowHeight - 100;
            },
            bindLogin() {
                /**
                 * 客户端对账号信息进行一些必要的校验。
                 * 实际开发中，根据业务需要进行处理，这里仅做示例。
                 */
                if (this.account.length < 5) {
                    uni.showToast({icon: 'none',title: '账号最短为 5 个字符'});
                    return;
                }
                if (this.password.length < 6) {
                    uni.showToast({icon: 'none',title: '密码最短为 6 个字符'});
                    return;
                }
                const data = {
                    account: this.account,
                    password: this.password
                };
				var that = this;
				uni.request({
					url:"http://192.168.3.125:8080/ams/system/distribute.htm?module=login&username="+that.account+"&password="+that.password+"&methodyd=1",
					success: (res) => {
						var data = res.data;
						if(data.code == 1){
							setUserInfo(data.user);
							this.login();
							uni.switchTab({
								url:"/pages/tabBar/mine/mine"
							})
						}else{
							uni.showToast({
								icon: 'none',
								title: data.message,
							});
							this.account = "";
							this.password = "";
						}
					}
				});
            },
            oauth(value) {
                uni.login({
                    provider: value,
                    success: (res) => {
                        uni.getUserInfo({
                            provider: value,
                            success: (infoRes) => {
                                /**
                                 * 实际开发中，获取用户信息后，需要将信息上报至服务端。
                                 * 服务端可以用 userInfo.openId 作为用户的唯一标识新增或绑定用户信息。
                                 */
                                this.toMain(infoRes.userInfo.nickName);
                            }
                        });
                    },
                    fail: (err) => {
                        console.error('授权登录失败：' + JSON.stringify(err));
                    }
                });
            },
            toMain(userName) {
                this.login(userName);
                if (this.forcedLogin) {
                    uni.reLaunch({
                        url: '../main/main',
                    });
                } else {
                    uni.navigateBack();
                }

            }
        },
        onReady() {
            this.initPosition();
            this.initProvider();
        }
    }
